import SpringBoop from "./spring-boop";

/**
 * GenericHoverSpring
 * -----------------
 * Generic, per-element, per-property spring animations triggered by hover/touch.
 */
export default class HoverBoop {
  /**
   * @param {string} selector - CSS selector for elements
   * @param {object} options
   *    tension - spring stiffness
   *    friction - spring damping
   *    boop - { property: targetValue } map, e.g. { rotate: 15, scale: 1.1 }
   */
  constructor(selector, { tension = 0.2, friction = 0.7, boop = {}, triggerOnParent = true } = {}) {
    this.elements = document.querySelectorAll(selector);
    this.triggerOnParent = triggerOnParent;
    this.config = { tension, friction, boop };

    this._bindEvents();
  }

  _bindEvents() {
    this.elements.forEach(el => {
      // Initialize initial values for all properties (0 for rotate/translate, 1 for scale)
      const initial = Object.fromEntries(
        Object.keys(this.config.boop).map(key => [key, key.includes("scale") ? 1 : 0])
      );

      // Create a SpringBoop for this element
      const spring = new SpringBoop(initial, this.config);

      spring.onUpdate(vals => {
        el.style.transform = this._buildTransformString(vals);
      });

      const trigger = () => spring.trigger();

      const target = this.triggerOnParent && el.parentElement ? el.parentElement : el;
      target.addEventListener("mouseenter", trigger);
      target.addEventListener("touchstart", trigger, { passive: true });
    });
  }

  _buildTransformString(vals) {
    return Object.entries(vals)
      .map(([key, val]) => {
        if (key.startsWith("rotate")) return `rotate(${val}deg)`;
        if (key.startsWith("scale")) return `${key}(${val})`;
        if (key.startsWith("translate")) return `${key}(${val}px)`;
        return "";
      })
      .join(" ");
  }

  static initialize(selector = ".icon", options = {}) {
    new HoverBoop(selector, options);
  }
}
